function Messenger() {
  const api = useAPI()

  const {user} = useSelector(state => state.session)
  const ref = useRef()
  let scrollPosition = useScrollPosition();
  const [toggleChatBox, setToggleChatBox] = useState(true);
  const [openMsg, setOpenMsg] = useState(false);
  const [messages, setMessages] = useState(null)
  const [messageText, setMessageText] = useState('')
  const [selectedUser, setSelectedUser] = useState(null)
  
  const handleStartChart = id => e =>{
    let socket
    if(!openMsg){
      setOpenMsg(true)
    }

    if(ref.current){
      socket = ref.current
      if(selectedUser){
        socket?.emit("chat::end",selectedUser)
      }
      socket?.emit("chat::start",id,(inboxes)=>{
        
        setMessages(inboxes)
      })
      setSelectedUser(id)
    }
  }

  const handleMsgChange = e =>{
    
    e.target.value&&setMessageText(e.target.value)
  }

  const handleSocketConnection = ()=>{
    const socket = api.SocketClient.getAuthSocket()
    socket.on("connect",()=>{
      ref.current = socket
    })
    return socket
  }


  const handleKeyDown = e =>{
    if(e.key==="Enter"){
      if(ref.current&&selectedUser){
        ref.current.emit("chat::message",{
          message:messageText,
          receiverId:selectedUser
        },(message)=>{
          
          setMessages(prev=>prev?[...prev,message]:[message])
        })
      }
      setMessageText('')

    }
  }
  useEffect(() => {
    const socket = handleSocketConnection()
    return () => {
      api.abort()
      socket.disconnect()

    }
  }, [])

  return (
    <div className="">
      <div className="chat-type-header">Type</div>
      <ChatGrid>
        {/* Chat users */}
        <ChatContacts className="chat-users contacts_card dz-chat-user-box">
          <PerfectScrollbar  >
            <ul className="contacts">
              <li className="contact-first-letter">A</li>
              <li className="contact_single" onClick={handleStartChart("4b76a7b4-e298-4fc6-aa15-fb7fa00872c2")}>
                <div className="contact_single_info bd-highlight">
                  <div className="img_cont">
                    <img
                      src={avatar1}
                      className="rounded-circle user_img"
                      alt=""
                    />
                    {/* <span className="online_icon"></span> */}
                    <span className="online_icon offline"></span>
                  </div>
                  <div className="user_info">
                    <span>Archie Parker</span>
                    <p>Kalid is online</p>
                  </div>
                </div>
              </li>
            </ul>
          </PerfectScrollbar>
        </ChatContacts>

        {/* Chat list*/}
        <ChatList>
          <PerfectScrollbar
            className={`card-body contacts_body p-0 dz-scroll  ${
              toggleChatBox ? "ps ps--active-y" : ""
            }`}
          >
            <ChatBubble send>
              <div class="img_cont_msg">
                <img
                  src="data:image/jpeg;base64,"
                  class="rounded-circle user_img_msg"
                  alt=""
                />
              </div>
              <div className="msg_container">
                Hi, how are you doing today?
                <span class="msg_time">8:40 AM, Today</span>
              </div>
            </ChatBubble>
            <ChatBubble>
              <div class="img_cont_msg">
                <img
                  src="data:image/jpeg;base64,"
                  class="rounded-circle user_img_msg"
                  alt=""
                />
              </div>
              <div className="msg_container">
                Hi, I am great, you? Lorem ipsum dolor sit amet consectetur
                adipisicing elit. Architecto itaque sunt commodi. Optio
                reprehenderit magnam a rem, vero mollitia iste cum repellendus
                repellat quaerat inventore blanditiis fuga modi. Laudantium,
                earum!
                <span class="msg_time">8:40 AM, Today</span>
              </div>
            </ChatBubble>
            {
              messages?messages.map((message,index,arr)=>(
                <ChatBubble send={message.sender_id===user.id} key={message.id} ref={ref=>{
                  index+1===arr.length&&ref?.scrollIntoView()
                }}>
                  <div class="img_cont_msg">
                    <img
                      src="data:image/jpeg;base64,"
                      class="rounded-circle user_img_msg"
                      alt=""
                    />
                  </div>
                  <div className="msg_container">
                    {message.text}
                    <span class="msg_time">{formatDistance(new Date(message.createdAt), new Date(), { addSuffix: true })}</span>
                    {/* <span class="msg_time">8:40 AM, Today</span> */}
                  </div>
                </ChatBubble>
              )):null
            }
          </PerfectScrollbar>
        </ChatList>

        <ChatForm>
          <div>
            <button type="botton">
              <span className="simple-paper-clip"></span>
            </button>
          </div>
          <div className="chat-message-input">
            <input
              onChange={handleMsgChange}
              value={messageText}
              type="text"
              name="message_box"
              id="message_box"
              className="message_box"
              placeholder="Enter message here..."
              onKeyDown={handleKeyDown}
            />
          </div>
          <div>
            <button type="botton">
              <span className="simple-microphone"></span>
            </button>
          </div>
        </ChatForm>
      </ChatGrid>
    </div>
  );
}
